/**
 * @description 签到助力页（分享入口页面）
 * @author 顾超<beyondc@foxmail.com>
 */
// 组件
import AidSignPanel from './Sign/AidSignPanel'
import MerchantRecommend from './Sign/MerchantRecommend'

// 接口
import {
    user_detail,
    checkin_record,
    near_merchant,
    aid_checkin
} from '@/api/index'

// 依赖
const wx = require('weixin-js-sdk')

export default {
    name: 'SignPage',
    components: {
        AidSignPanel,
        MerchantRecommend
    },
    data() {
        return {
            lon: '',
            lat: '',
            info: {},
            list: [],
            merchantList: []
        }
    },
    created() {
        this.shareId = this.$route.query ? this.$route.query.shareid : ''
        this.initPage()
    },
    methods: {
        initPage() {
            this.getInfo()
            this.getList()
        },
        getInfo() {
            user_detail({customer_id: this.shareId}).then(({ data, res }) => {
                this.info = data ? data : {}
            })
        },
        getList() {
            checkin_record({customer_id: this.shareId}).then(({ data, res }) => {
                this.list = data ? data : []
                this.getStoreList()
            })
        },
        getStoreList() {
            const _this = this
            this.getLocation().then(() => {
                const params = {
                    lon: _this.lon,
                    lat: _this.lat
                }
                near_merchant(params).then(({ data, res }) => {
                    _this.merchantList = data ? data : []
                })
            })
        },
        getLocation() {
            const _this = this
            return new Promise((resolve, reject) => {
                wx.getLocation({
                    type: 'wgs84',
                    success: res => {
                        _this.lon = res.longitude
                        _this.lat = res.latitude
                        resolve()
                    }
                })
            })
        },
        handleAidSign() {
            const data = {customer_id: this.shareId}
            aid_checkin(data).then(({ res }) => {
                this.getList()
            })
        }
    }
}



