/**
 * @description 登录信息（组件）
 * @author 顾超<beyondc@foxmail.com>
 */
export default {
    name: 'UserInfo',
    props: {
        info: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            avatar: require('@/assets/avatar.jpg'),
            iconGift: require('@/assets/icon_gift.png')
        }
    },
    methods: {
        toWelfare() {
            /**
             * @description 我的福利点击事件的返回函数
             *  - 跳转到我的福利列表页面
             * @return (void)
             */
            window.console.log('点击了我的福利')
            this.$router.push({ name: 'my_welfare' })
        }
    }
}



