/**
 * @description 签到状态（组件）
 * @author 顾超<beyondc@foxmail.com>
 */
export default {
    name: 'WSignItem',
    props: {
        signed: {
            type: Boolean,
            required: true
        },
        missed: {
            type: Boolean,
            default: false
        },
        final: {
            type: Boolean,
            default: false
        },
        label: {
            type: String,
            required: true
        }
    },
    computed: {
        badge () {
            let label
            if (this.missed) {
                label = '待补签'
            } else if (this.final) {
                label = '领啤酒'
            } else {
                label = false
            }
            return label
        },
        icon () {
            let src
            if (this.final) {
                src = require('@/assets/sign_gift.png')
            } else {
                src = require('@/assets/sign_right.png')
            }
            return src
        }
    }
}



