/**
 * @description 签到页（入口页面）
 * @author 顾超<beyondc@foxmail.com>
 */
// 组件
import SignPanel from './Sign/SignPanel'
import UserInfo from './Sign/UserInfo'
import MerchantRecommend from './Sign/MerchantRecommend'

// 接口
import {
    user_detail,
    checkin_record,
    near_merchant,
    checkin,
    re_checkin,
    wx_pay,
    is_supplement,
    get_ad_poster
} from '@/api/index'
const wx = require('weixin-js-sdk')

// 依赖
import Cookies from 'js-cookie'
import {startWXPay, setWxShare} from '@/plugins/wechat-sdk'


import baseUrl from '@/utils/doman'

export default {
    name: 'SignPage',
    components: {
        SignPanel,
        UserInfo,
        MerchantRecommend
    },
    data() {
        return {
            lon: '',
            lat: '',
            info: {},
            list: [],
            merchantList: [],
            payDialog: {
                show: false,
                amount: 5,
                options: {}
            },
            shareOptions: {
                title: '分享标题',
                desc: '分享描述',
                shareId: '',
                imgUrl: `${window.location.origin}/assets/icon.jpg`
            },
            isAidSign: false,
            adDialog: {
                show: false,
                url: `${baseUrl}v1/merchants/poster?access_token=${Cookies.get('Authorization')}`
            }
        }
    },
    created() {
        this.initPage()
    },
    methods: {
        initPage() {
            this.getInfo()
            this.getList()
            this.getSignStatus()
        },
        getInfo() {
            user_detail().then(({ data, res }) => {
                this.info = data ? data : {}
                this.shareOptions.shareId = data && data.id
                // 分享需要当前用户信息所以要拿到用户信息才能设置
                this.setShare()
            })
        },
        getList() {
            checkin_record().then(({ data, res }) => {
                this.list = data ? data : []
                this.getStoreList()
            })
        },
        getStoreList() {
            const _this = this
            this.getLocation().then(() => {
                const params = {
                    lon: _this.lon,
                    lat: _this.lat
                }
                near_merchant(params).then(({ data, res }) => {
                    _this.merchantList = data ? data : []
                })
            })
        },
        getLocation() {
            const _this = this
            return new Promise((resolve, reject) => {
                wx.getLocation({
                    type: 'wgs84',
                    success: res => {
                        _this.lon = res.longitude
                        _this.lat = res.latitude
                        resolve()
                    }
                })
            })
        },
        getSignStatus() {
            is_supplement().then(({data, res}) => {
                this.isAidSign = data
            })
        },
        handleSign() {
            this.adDialog.show = true
        },
        handleCloseAd() {
            checkin().then(({ res }) => {
                this.getList()
            })
            this.adDialog.show = false
        },
        handleResign() {
            this.payDialog.show = false
            re_checkin().then(res => {
                console.log('重新签到执行成功', res)
                this.getList()
            })
        },
        handlePay() {
            wx_pay().then(({data}) => {
                data = JSON.parse(data)
                this.payDialog.options = data
                this.payDialog.amount = ((data.payFee)/100).toFixed(2)
                this.payDialog.show = true
            })
        },
        handleCancel() {
            this.payDialog.show = false
        },
        handleConfirm() {
            startWXPay(this.payDialog.options).then(res => {
                console.log('支付成功', res)
                this.payDialog.show = false
                this.$vux.toast.show({
                    type: 'text',
                    text: '支付成功'
                })
                this.getList()
            })
        },
        setShare() {
            setWxShare(this.shareOptions)
        }
    }
}



