/**
 * @description 签到板（组件）
 * @author 顾超<beyondc@foxmail.com>
 */
// 组件
import WCard from '@/components/WCard'
import WSignItem from '@/components/WSignItem'
export default {
    name: 'SignPanel',
    components: {
        WCard,
        WSignItem
    },
    props: {
        list: {
            type: Array,
            required: true
        },
        isAid: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            today: new Date(),
            currentItem: {},
            shareDialog: {
                show: false
            },
            rulesDialog: {
                show: false
            }
        }
    },
    computed: {
        signList() {
            const _this = this
            const list = this.list.map((item, index, arr) => {
                if (index === arr.length - 1) {
                    item.final = true
                }
                switch (item.status) {
                    case 'U':
                        item.signed = false
                        break
                    case 'A':
                        item.signed = true
                        break
                    default:
                        break
                }
                const diff = this.isMissed(
                    this.today,
                    new Date(item.need_checkin_time)
                )
                item.missed = diff >= 1 && !item.signed
                item.label = `第${item.day}天`
                if (diff === 0) {
                    _this.currentItem = item
                }
                return item
            })
            return list
        },
        signDone() {
            return this.currentItem.signed
        },
        haveMissed() {
            const missedIndex = this.signList.findIndex(item => item.missed)
            return missedIndex > -1
        },
        signedTime() {
            let time = 0
            this.signList.forEach(item => {
                if (item.signed) {
                    time += 1
                }
            })
            return time
        }
    },
    methods: {
        isMissed(today, needDate) {
            today = this.returnDay(today)
            needDate = this.returnDay(needDate)
            const diff = (today - needDate) / (1000 * 60 * 60 * 24)
            return diff
        },
        returnDay(date) {
            let y = date.getFullYear()
            let m = date.getMonth() + 1
            m = m > 9 ? m : `0${m}`
            let d = date.getDate()
            d = d > 9 ? d : `0${d}`
            return new Date(`${y}/${m}/${d}`)
        },
        handleSign() {
            this.$emit('sign')
        },
        handleResign() {
            this.$emit('resign')
        },
        handleShare() {
            this.shareDialog.show = true
        },
        handlePay() {
            this.$emit('pay')
        },
        handleRules() {
            this.rulesDialog.show = true
        }
    }
}



